import React, { useEffect, useState } from "react";
import { Card, CardBody, CardHeader, Col, Row, Table } from "reactstrap";
import { toast, ToastContainer } from "react-toastify";
import axios from "axios";
import Spinner from "reactstrap/lib/Spinner";

const Favorites = (props) => {
  const { id } = props.match.params;
  const [favorites, setFavorites] = useState([]);
  if (!id) {
    props.history.push("/dashboard");
  }

  useEffect(() => {
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query alluser($userId:ID,$page:Int,$limit:Int){
            getUsers(userId:$userId,page:$page,limit:$limit){
              _id,
              phone,
             favorite{
              product{
                fname,
                ename,
                orginal,
                attribute{
                  color,
                  price
                }
              },
            }
          }
          }
        `,
        variables: {
          userId: id,
          page: 1,
          limit: 30,
        },
      },
    })
      .then((res) => {
        if (res.data.errors) {
          toast.error("خطا در دریافت اطلاعات");
        } else {
          setFavorites(res.data.data.getUsers);
        }
      })
      .catch((err) => console.log(err));
  }, [id]);

  return (
    <div className="animated fadeIn">
      <ToastContainer />
      <Row>
        <Col lg={12}>
          <Card>
            <CardHeader>
              <strong>
                <i className="icon-info pr-1"></i>ایدی کاربر:{" "}
                {props.match.params.id}
              </strong>
            </CardHeader>
            <CardBody>
              <Row>
                <Table responsive hover>
                  <thead>
                    <tr>
                      <th>نام محصول</th>
                      <th>عکس</th>
                      <th>رنگ</th>
                      <th>قیمت</th>
                    </tr>
                  </thead>
                  <tbody>
                    {favorites.length > 0 ? (
                      favorites ? (
                        favorites[0].favorite.map((item, index) => (
                          <tr key={index}>
                            <td>{item.product.fname}</td>
                            <td>
                              <img
                                src={`${process.env.REACT_APP_BASE_URL}${item.product.orginal}`}
                                alt={item.product.orginal}
                                style={{ width: "70px", borderRadius: "25px" }}
                              />
                            </td>
                            <td>{item.product.attribute[0].color}</td>
                            <td>{item.product.attribute[0].price}</td>
                          </tr>
                        ))
                      ) : (
                        "کاربر هیچ مصحولی را به علاقه مندی خود اضافه نکرده است"
                      )
                    ) : (
                      <Spinner size="sm" />
                    )}
                  </tbody>
                </Table>
              </Row>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default Favorites;
